<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="true" ModuleName="RateSchedule" SubModuleCode="" :LegendArray="[]"
        :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
        IdentityColumn="RATE_SCHEDULE_ID" :listType="['List']" :ListData="RateScheduleData" :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
        IsSearchFieldsOnTop="true" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :HeaderText="$t('RateSchedule')"
        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{ data }">  
             <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                    <span>
                        {{ $options.filters.formatDateTime(data.row.CREATED_AT) }}
                    </span>
                </template>
                 <template v-if="data.column.COLUMN_NAME == 'MODIFIED_AT'">
                    <span>
                        {{ $options.filters.formatDateTime(data.row.MODIFIED_AT) }}
                    </span>
                </template>         
            <template v-if="data.column.COLUMN_NAME == 'STATUS'">
                <td class="text-center single-action">
                    <div class="cstm-drop-btn">
                        <select v-model="data.row.STATUS_ID" @change="UpdataStatus(data.row.STATUS_ID, data.row.RATE_SCHEDULE_ID);" :class="data.row.STATUS_ID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                            <option value="1001" selected="selected">{{ $t("Active") }}</option>
                            <option value="1002">{{ $t("Inactive") }}</option>
                        </select>
                    </div>
                </td>
            </template>
        </template>
    </tg-list>
    <AddRate v-if="isShowAddRate" :EditData="EditData"></AddRate>
    <BulkUpdateRate v-if="isBulkUpdateRate" :EditData="EditData"></BulkUpdateRate>
    <Copy v-if="isCopyRate" :EditData="EditData"></Copy>
</section>
</template>

<script>
import AddRate from "./AddRate.vue";
import BulkUpdateRate from "./BulkUpdateRate.vue";
import Copy from "./Copy.vue";
import Schema from "../RateSchedule/Schema.js";
import DataService from "../../services/DataService.js";
import * as XLSX from 'xlsx';

export default {
    components: {
        AddRate,
        BulkUpdateRate,
        Copy
    },
    data() {
        return {
            isLoading: false,
            Headers: [{
                    "COLUMN_NAME": "CLIENT_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Client',
                    "DISPLAY_ORDER": 1,
                    // settings: {
                    //     isInSlot: false
                    // }
                },
                {
                    "COLUMN_NAME": "WORK_CODE",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Work Code',
                    "DISPLAY_ORDER": 2,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                {
                    "COLUMN_NAME": "UTILITY_NAME",
                    "DATA_TYPE": "Date",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Utility Name',
                    "DISPLAY_ORDER": 3,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                {
                    "COLUMN_NAME": "STATE_NAME",
                    "DATA_TYPE": "Date",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'State',
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: false
                    }
                },
                {
                    "COLUMN_NAME": "Billing_Code",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Customer Work Code',
                    "DISPLAY_ORDER": 5,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                 {
                    "COLUMN_NAME": "AMOUNT",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Rate',
                    "DISPLAY_ORDER": 6,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                 {
                    "COLUMN_NAME": "STATUS",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Status',
                    "DISPLAY_ORDER": 7,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "START_DATE",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Start Date',
                    "DISPLAY_ORDER": 8,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                {
                    "COLUMN_NAME": "END_DATE",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'End Date',
                    "DISPLAY_ORDER": 9,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                {
                    "COLUMN_NAME": "CREATED_AT",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Created AT',
                    "DISPLAY_ORDER": 10,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "CREATED_BY",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Created By',
                    "DISPLAY_ORDER": 11,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                {
                    "COLUMN_NAME": "MODIFIED_BY",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Modified By',
                    "DISPLAY_ORDER": 12,
                    // settings: {
                    //     isInSlot: true
                    // }
                },
                {
                    "COLUMN_NAME": "MODIFIED_AT",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Modified Date',
                    "DISPLAY_ORDER": 13,
                    settings: {
                        isInSlot: true
                    }
                }
            ],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            noRecords: false,
            searchCondition: '',
            RateScheduleData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            leftSearchFields: Schema.leftsearchSchema,
            CompanyId: null,
            UserId: null,
            searchBy: '',
            search: '',
            isShowAddRate: false,
            isBulkUpdateRate: false,
            isCopyRate: false,
            EditData:[],
            clientId:''
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.UserId = this.GetUserInfo.ID;
        await vm.FetchData();
        Schema.bindleftCommonSearchdropdown(this,'Client');
        Schema.bindleftCommonSearchdropdown(this,'WORKCODE');
        Schema.bindleftCommonSearchdropdown(this,'STATE');
    },
    methods: {
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case "Edit":
                    this.EditAddRate(item.RATE_SCHEDULE_ID);
                    break;
            }
        },
        leftCommonSearch: function (value) {
            debugger;
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "Inactive":
                    this.UpdataStatus(1002, '');
                    break;
                case "AddRateSchedule":
                    this.AddRateSchedule();
                    break;
                case "Update":
                    this.BulkUpdateRate();
                    break;
                case "CopyRate":
                    this.CopyRate();
                    break;
                case "ExportToExcel":
                    this.ExportToExcel();
                    break;
                case "ViewHistory":
                    this.ViewHistory();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        CopyRate() {
            var vm = this;
            vm.EditData =[];
            vm.isCopyRate = true;
        },
        BulkUpdateRate () {
            debugger;
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(async function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            let ids = val.split(',').map(Number);
             if (val.length > 0) {
                 vm.EditData = vm.RateScheduleData.filter(x => ids.includes(x.RATE_SCHEDULE_ID));
                 vm.isBulkUpdateRate = true;
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
            }           
        },
        AddRateSchedule () {
            var vm = this;
            vm.EditData =[];
            vm.isShowAddRate = true;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: async function () {
            var vm = this;
            var params = '';
            vm.isLoading = true;          
             var params = `clientId=${vm.clientId}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageNum=${vm.PageNumber}&pageSize=${vm.PageSize}&Search=${vm.searchCondition}&userDate=${Date.now()}`            
            await DataService.GetRateSechduleList(params).then((response) => {
                if (response.data.result.length > 0) {
                    vm.isLoading = false;
                    vm.RateScheduleData = JSON.parse(response.data.result);
                    vm.TotalRecords = vm.RateScheduleData[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;                   
                    vm.noRecords = false;
                } else {
                    vm.RateScheduleData = [];
                    vm.noRecords = true;
                    vm.TotalRecords = 0
                    vm.isLoading = false;
                }
            })
        },
         UpdataStatus(StatusID, ID) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(async function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });

            vm.confirmR(vm.$t('AreYouSureToUpdateStatus'), true, false, function (result) {
                var postJSON = {
                    ID: val || ID,
                    StatusId: StatusID,
                    IsFor: "TALYGEN_BE_RATE_SCHEDULE",
                    Module: "RateSchedule",
                    Submodule: "Rate Schedule",
                };
                var objectPostString = JSON.stringify(postJSON);
                DataService.CommonUpdateStatus(objectPostString).then(async response => {
                    if (response.status == 200) {
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vm.listheaderbuttons[1].isdisabled = true;
                        vm.listheaderbuttons[2].isdisabled = true;
                        vm.listheaderbuttons[3].isdisabled = true;
                        vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                        vm.RuleFlagListing();

                    }
                });
            })
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("RateSchedule")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_BE_RATE_SCHEDULE&primaryKey=RATE_SCHEDULE_ID`;
                    DataService.Delete(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('RateSchedule')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('RateSchedule')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        EditAddRate(id){
            var vm=this;            
            vm.EditData = vm.RateScheduleData.find(x => x.RATE_SCHEDULE_ID === id);
            vm.isShowAddRate=true;
        },
        ViewHistory() {
            var vm = this;
            this.$router.push({path:'/RateSchedule/RateScheduleHistory'});
        },

        async ExportToExcel() {
            debugger;
            var vm = this;
            vm.isLoading = true;
            vm.PageSize = vm.TotalRecords;
            await vm.FetchData();
            vm.PageSize = 10;

            var objTemplate = {
                'Client Name': '',
                'Rate Schdule Id': '',
                'Work Code': '',
                'Utility Name': '',
                'Rate': '',
                'State': '',                
            };

            var dataArray = [];
            try {
                var RateScheduleArray = vm.RateScheduleData;

                RateScheduleArray.forEach(function (item) {
                    var newObj = Object.assign({}, objTemplate);
                    newObj['Client Name'] = item.CLIENT_NAME || '';
                    newObj['Rate Schdule Id'] = item.RATE_SCHEDULE_ID || '';
                    newObj['Work Code'] = item.WORK_CODE || '';
                    newObj['Utility Name'] = item.UTILITY_NAME || '';                   
                    newObj['Rate'] = item.AMOUNT || '';
                     newObj['State'] = item.STATE_NAME || '';
                    dataArray.push(newObj);
                });
            } catch (e) {
                console.error("Invalid JSON string in GetExcelListing:", e);
            }

            if (dataArray.length > 0) {
                // Create a new worksheet
                const worksheet = XLSX.utils.json_to_sheet([]);

                // Create a new workbook
                const workbook = XLSX.utils.book_new();

                // Create a title row with "User Report" and date-time
                const currentDateTime = new Date().toLocaleString('en-US');
                const titleRow = [
                    ['RateSchedule Report', '', '', '', currentDateTime] // Span from A1 to E1
                ];

                // Append title to the worksheet at the first row (A1 to E1)
                XLSX.utils.sheet_add_aoa(worksheet, titleRow, { origin: 'A1' });

                // Merge cells from A1 to D1 for the title
                worksheet['!merges'] = [
                    { s: { r: 0, c: 0 }, e: { r: 0, c: 3 } } // Merges A1:D1
                ];

                // Set styles for the title and date-time row
                worksheet['A1'].s = {
                    font: { bold: true, size: 20, color: { rgb: "000000" } }, // Increased size to 16
                    alignment: { horizontal: "left" },
                };

                worksheet['E1'].s = {
                    font: { bold: true, size: 20, color: { rgb: "000000" } }, // Increased size to 14
                    alignment: { horizontal: "right" },
                };

                // Add headers manually in A2
                const header = Object.keys(dataArray[0]);
                XLSX.utils.sheet_add_aoa(worksheet, [header], { origin: 'A2' });

                // Append the data starting from A3
                XLSX.utils.sheet_add_json(worksheet, dataArray, { origin: 'A3', skipHeader: true });

                // Apply styles to the header row (A2)
                const range = XLSX.utils.decode_range(worksheet['!ref']);
                for (let C = range.s.c; C <= range.e.c; ++C) {
                    const cell_address = XLSX.utils.encode_cell({ c: C, r: 1 }); // Header row index (1-based)
                    if (!worksheet[cell_address]) continue;
                    worksheet[cell_address].s = {
                        font: { bold: true, size: 18, color: { rgb: "000000" } }, // Increased size to 12
                        fill: { fgColor: { rgb: "D3D3D3" } }, // Gray background
                        alignment: { horizontal: "center" },
                    };
                }

                // Adjust column widths
                const colWidths = header.map(col => ({
                    wpx: Math.max(
                        col.length * 10, // Base width (adjust as needed)
                        ...dataArray.map(row => (row[col] || '').toString().length * 10) // Calculate max width
                    )
                }));

                worksheet['!cols'] = colWidths;

                // Append the worksheet to the workbook
                XLSX.utils.book_append_sheet(workbook, worksheet, "RateSchedule List");

                // Save the workbook to a file
                XLSX.writeFile(workbook, "RateSchedule-List.xlsx");
            } else {
                console.error("No data available to export");
            }
            vm.isLoading = false;
        },
    }
}
</script>