<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('Update') }}</h4>
                    <span class="user-guide">
                        <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button" aria-expanded="false" aria-controls="collapseExample"> <em class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('UgHiringAddDepartment')"></div>
                            </div>
                        </div>
                    </span>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" ref="ResourceRate" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        EditData: {
            type: Array,
            required: false,
        },
    },
    data() {
        return {
            isLoading: false,
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            companyId: null,
            userId: null,
            PageSize: 100,
            PageNumber: 1,
            searchCondition: '',
            GroupNewData: [],
            UtiltityNames: [],
            StateNames: [],
            WorkCodes: [],
            StatusData: [],
            FormSchema: [{
                    layoutType: "double",
                    Data: [                        
                        {
                            astype: "DateField",
                            label: this.$t('Start Date (M/D/YYYY)'),
                            name: "StartDate",
                            value: new Date(),
                            visibility: true,
                            placeholder: this.$t('StartDate'),
                            config: {
                                format: "MM/DD/YYYY"
                            },
                            validationRules: {
                                "required": true
                            },
                            visibility: true,
                            disabled: false,
                        },                        
                        {
                            astype: "DateField",
                            label: this.$t('End Date (M/D/YYYY)'),
                            name: "EndDate",
                            value: new Date(),
                            visibility: true,
                            placeholder: this.$t('EndDate'),
                            config: {
                                format: "MM/DD/YYYY"
                            },
                            validationRules: {
                                "required": true
                            },
                            visibility: true,
                            disabled: false,
                        }, 
                        {
                            astype: "RadioField",
                            label: this.$t('previous records End Date will be updated as'),
                            name: "PreviousRecords",
                            visibility: true,
                            disabled: false,
                            value: "",
                            config: {
                                options: [
                                    { value: "1", name: "One Time" }
                                ]
                            }
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('Do you want to mark previous Records as Inactive?'),
                            name: "RecordsInactive",
                            visibility: true,
                            disabled: false,
                            value: "",
                            config: {
                                options: [{ value: "1", name: "One Time" }]
                            }
                        },                       

                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Comment'),
                        name: "Comment",
                        value: "",
                        placeholder: this.$t('Enter Comment'),
                        validationRules: {
                            //"required": false
                        }
                    }]
                }

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Set New Dates",
                    isVisible: true,
                },                
            ]
        }
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        // if(vm.EditData.RATE_SCHEDULE_ID>0){            
        //     (vm.FormSchema)[0].Data[1].value=vm.EditData.AMOUNT;            
        //     (vm.FormSchema)[0].Data[3].value=vm.$options.filters.formatDate(vm.EditData.END_DATE)
        // }
    },
    methods: {      

        
        onSubmit(formfields, val) {
            var vm = this;
            var vm = this;
            const result = vm.$refs.ResourceRate.validateForm.validate();
            if (result) {
                debugger;
                var objData = {
                    RateScheduleId: vm.EditData.map(x => x.RATE_SCHEDULE_ID).join(","),
                    ClientId: vm.EditData.length > 0 && vm.EditData[0].CLIENT_ID > 0
                        ? vm.EditData[0].CLIENT_ID
                        : 0,
                    
                    StartDate: formfields.StartDate,
                    EndDate: formfields.EndDate,
                    Comment:formfields.Comment,                        
                };

                debugger;
                var obj = JSON.stringify(objData);
                var params = `objData=${encodeURIComponent(obj)}`;
                DataService.BulkUpdateRate(obj).then(response => {
                    if (response.data != null) {
                        const Result = JSON.parse(response.data)[0];
                        debugger
                        if (parseInt(Result.RESULT) === 1) {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('RateSchedule')]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        } else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        }
                    }
                });

            }
        },
        Close: function () {
            this.$parent.FetchData();
            this.$parent.isCopyRate = false;
        }
    }
}
</script>
