<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('addrate') }}</h4>
                    <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG"
                                role="button" aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgHiringAddDepartment')"></div>
                                </div>
                            </div>
                        </span>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" ref="ResourceRate" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
     props: {
        EditData: {
            type: Array,
            required: false,
        },
    },
    data() {
        return {
            isLoading: false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            companyId: null,
            userId: null,
            PageSize: 100,
            PageNumber: 1,
            searchCondition: '',
            GroupNewData: [],
            UtiltityNames: [],
            StateNames: [],
            WorkCodes: [],
            StatusData: [],
            FormSchema: [{
                    layoutType: "triple",
                    Data: [
                        {
                        astype: "SelectField",
                        label: this.$t('ContractCompanyName'),
                        name: "Contract",
                        value: "",
                        placeholder: this.$t('Select Contract'),
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                            onChange: this.GetUtiltityNames
                        },
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('UtilityName'),
                        name: "UtilityName",
                        value: "",
                        placeholder: this.$t('Select Utility Name'),
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                            
                        },
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('State'),
                        name: "State",
                        value: "",
                        placeholder: this.$t('Select State'),
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('WorkCode'),
                        name: "WorkCode",
                        value: "",
                        placeholder: this.$t('Select Work Code'),
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    },
                    {
                        astype: "DateField",
                        label: this.$t('Start Date (M/D/YYYY)'),
                        name: "StartDate",
                        value: new Date(),
                        visibility: true,
                        placeholder: this.$t('StartDate'),
                        config: {
                            format: "MM/DD/YYYY"
                        },
                        validationRules: {
                            "required": true
                        }
                    },
                    {
                        astype: "DateField",
                        label: this.$t('End Date (M/D/YYYY)'),
                        name: "EndDate",
                        value: new Date(),
                        visibility: true,
                        placeholder: this.$t('EndDate'),
                        config: {
                            format: "MM/DD/YYYY"
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('Rate'),
                        name: "Rate",
                        value: "",
                        placeholder: this.$t('Enter Rate'),
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: "",
                        placeholder: this.$t('Select Status'),
                        validationRules: {
                            //"required": false
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    },
                    {
                        astype: "TextField",
                        label: this.$t('CustomerWorkCode'),
                        name: "CustomerWorkCode",
                        value: "",
                        placeholder: this.$t('Enter Customer Work Code'),
                        validationRules: {
                           // "required": false
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Comment'),
                        name: "Comment",
                        value: "",
                        placeholder: this.$t('Enter Comment'),
                        validationRules: {
                            //"required": false
                        }
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                }
            ]
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.GetParentClientGroupNew();
        vm.GetStateListByCountryId();
        vm.GetWorkCodes();
        vm.GetStatusList();
        debugger;
        if(vm.EditData.RATE_SCHEDULE_ID>0){
            (vm.FormSchema)[0].Data[0].value=vm.EditData.CLIENT_ID;
            (vm.FormSchema)[0].Data[6].value=vm.EditData.AMOUNT;
            (vm.FormSchema)[0].Data[8].value=vm.EditData.Billing_Code;
            await vm.GetUtiltityNames('client','',vm.EditData.CLIENT_ID);
            setTimeout(() => {
                debugger;
                setTimeout(() => {
                  (vm.FormSchema)[0].Data[1].value=vm.EditData.UTILITY_ID?.toString();  
                }, 1000);
                const states=vm.StateNames.find(x => x.ISO_CODE_3 == vm.EditData.STATE_CODE);              
                (vm.FormSchema)[0].Data[2].value=states.STATE_ID;
                (vm.FormSchema)[0].Data[3].value=vm.EditData.WORKCODE_ID?.toString();
                (vm.FormSchema)[0].Data[7].value=vm.EditData.STATUS_ID
            }, 500);          
            
            (vm.FormSchema)[1].Data[0].value=vm.EditData.COMMENT;
            (vm.FormSchema)[0].Data[4].value=vm.$options.filters.formatDate(vm.EditData.START_DATE)
            (vm.FormSchema)[0].Data[5].value=vm.$options.filters.formatDate(vm.EditData.END_DATE)
        }
    },
    methods: {

        GetParentClientGroupNew() {
            debugger
            var vm = this;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&reqFrom='CUSTOM'&search=${vm.searchCondition}&userId=${vm.GetUserInfo.ID}`;

            DataService.GetParentClientGroupNew(url).then((response) => {
                vm.ClientNameData = vm.convertApiTreeToFlatList(response.data.result);
                vm.ClientNameData.forEach((itm, index) => {
                    (vm.FormSchema)[0].Data[0].config.options.push({
                        name: itm.CHILD_NAME,
                        value: itm.CHILD_USER_ID
                    })
                    //if(vm.EditData[0].length>0){
                      //  (vm.FormSchema)[0].Data[0].value=vm.EditData[0].CLIENT_ID;
                    //}
                })
                vm.$refs.ResourceRate.UpdateKeyValue();
            });
        }, 

        convertApiTreeToFlatList(tree) {
            let flat = [];

            function walkParent(node) {
                // Push parent
                flat.push({
                    CHILD_USER_ID: node.parenT_ID,
                    CHILD_NAME: node.parenT_NAME,
                    PARENT_ID: null
                });

                // Push children
                if (node.children && node.children.length > 0) {
                    node.children.forEach(child => {
                        flat.push({
                            CHILD_USER_ID: child.chilD_USER_ID,
                            CHILD_NAME: child.chilD_NAME,
                            PARENT_ID: node.parenT_ID
                        });
                    });
                }
            }

            tree.forEach(root => walkParent(root));

            return flat;
        }, 
        GetUtiltityNames:async function (event, field, meta) {                    
            var vm = this;
            //var url = `clientId=${evt.target.value}`
            if(event=='client'){
                var url = `clientId=${meta}`
            }else{
                var url = `clientId=${field.value}`
            }  
            debugger;          
            await DataService.GetUtiltityNames(url).then((response) => {               
                if (response.data.result.length > 0) {                   
                    (vm.FormSchema)[0].Data[1].config.options = [];
                    vm.UtiltityNames =JSON.parse(response.data.result);
                    vm.UtiltityNames.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            name: `${itm.TEXT}`,
                            value: `${itm.VALUE}`
                        })
                    })
                    vm.$refs.ResourceRate.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false
            });
        },

        GetStateListByCountryId: function () {                     
            var vm = this;            
            var url = `countryId=${223}&includeCandaState=true`                       
            DataService.GetStateListByCountryId(url).then((response) => {               
                if (response.data.result.length > 0) {                    
                    (vm.FormSchema)[0].Data[1].config.options = [];
                    vm.StateNames =response.data.result;
                    vm.StateNames.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            name: `${itm.STATE_NAME }`,
                            value: `${itm.STATE_ID }`
                        })
                    })
                    vm.$refs.ResourceRate.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false
            });
        },

        GetWorkCodes: function () { 
            debugger;          
            var vm = this;                      
            DataService.GetWorkCodes().then((response) => {               
                if (response.data.result.length > 0) {
                    (vm.FormSchema)[0].Data[3].config.options = [];
                    vm.WorkCodes = (response.data.result);
                    vm.WorkCodes.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[3].config.options.push({
                            name: `${itm.Text}`,
                            value: `${itm.Value}`
                        })
                    })
                    vm.$refs.ResourceRate.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false
            });
        },

        GetStatusList() {
            var vm = this;
            vm.isLoading = true;
            try {
                DataService.GetClientStatusList().then((response) => {
                    if (JSON.parse(response.data) != null) {
                        vm.StatusData = JSON.parse(response.data);                    
                        vm.StatusData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[7].config.options.push({
                            name: `${itm.STATUS}`,
                            value: `${itm.STATUS_ID}`
                        })
                    })
                    vm.$refs.ResourceRate.UpdateKeyValue();
                    }
                    vm.isLoading = false;
                });
            } catch (error) {
                vm.isLoading = false;
            }
        },

        onSubmit(formfields, val) {
            var vm = this;
             var vm = this;
            const result =  vm.$refs.ResourceRate.validateForm.validate();
            if (result) {
                debugger;
                var objData = {
                        RateScheduleId: vm.EditData.RATE_SCHEDULE_ID > 0 ? vm.EditData.RATE_SCHEDULE_ID:0,                     
                        ClientId: formfields.Contract,
                        UtilityId: formfields.UtilityName,
                        WorkCodeId: formfields.WorkCode,
                        Rate: formfields.Rate,
                        StartDate: formfields.StartDate,
                        EndDate: formfields.EndDate,
                        State: formfields.State,
                        Comment:formfields.Comment,
                        Notes: formfields.Comment,
                        StatusId: formfields.Status,
                        BCode: formfields.CustomerWorkCode,
                        //CostCenter: $("#CostCenter").val(),
                };
                debugger;
                var obj = JSON.stringify(objData);
                var params = `objData=${encodeURIComponent(obj)}`;
                 DataService.AddUpdateRate(obj).then(response => {                    
                    if (response.data != null) {
                        const Result=JSON.parse(response.data)[0];
                        debugger
                        if (parseInt(Result.RESULT) === 1) {                            
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('RateSchedule')]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        } else if (Result.Success == 2) {
                             vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('RateSchedule')]), "Success", true, vm.$t('Alert'));
                             vm.Close();
                        }
                        else if (Result.Success == 0 && Result.Error== 'EXISTS') {
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('RateSchedule')]), "warning", true, vm.$t("Alert"));                            
                        } else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        }
                    }
                });

            }
        },
        Close: function () {
            this.$parent.FetchData();
            this.$parent.isShowAddRate = false;
        }
    }
}
</script>