<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('Update') }}</h4>
                    <span class="user-guide">
                        <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button" aria-expanded="false" aria-controls="collapseExample"> <em class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('UgHiringAddDepartment')"></div>
                            </div>
                        </div>
                    </span>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" ref="ResourceRate" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        EditData: {
            type: Array,
            required: false,
        },
    },
    data() {
        return {
            isLoading: false,
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            companyId: null,
            userId: null,
            PageSize: 100,
            PageNumber: 1,
            searchCondition: '',
            GroupNewData: [],
            UtiltityNames: [],
            StateNames: [],
            WorkCodes: [],
            StatusData: [],
            FormSchema: [{
                    layoutType: "four",
                    Data: [
                        {
                            astype: "toggleField",
                            label: this.$t('Want to Update Rate'),
                            name: "isvisible",
                            value: "",
                            placeholder: "",
                            config: {
                                onChange: this.RateRadion
                            },
                            visibility: true
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Rate'),
                            name: "Rate",
                            value: "",
                            placeholder: this.$t('Enter Rate'),
                            validationRules: {
                                "required": true
                            },
                            config: {
                                options: [],
                            },
                            visibility: true,
                            disabled: true,

                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Want to Update End Date'),
                            name: "isvisible",
                            value: "",
                            placeholder: "",
                            config: {
                                onChange: this.RadionEnddate
                            },
                            visibility: true
                        },
                        {
                            astype: "DateField",
                            label: this.$t('End Date (M/D/YYYY)'),
                            name: "EndDate",
                            value: new Date(),
                            visibility: true,
                            placeholder: this.$t('EndDate'),
                            config: {
                                format: "MM/DD/YYYY"
                            },
                            validationRules: {
                                "required": true
                            },
                            visibility: true,
                            disabled: true,
                        },
                        

                    ]
                },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Update",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                }
            ]
        }
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        // if(vm.EditData.RATE_SCHEDULE_ID>0){            
        //     (vm.FormSchema)[0].Data[1].value=vm.EditData.AMOUNT;            
        //     (vm.FormSchema)[0].Data[3].value=vm.$options.filters.formatDate(vm.EditData.END_DATE)
        // }
    },
    methods: {

       RateRadion(data) {
            const vm = this;
            (vm.FormSchema)[0].Data[1].disabled = !data;
            if (data) {
                if (vm.EditData.length > 0) {
                    (vm.FormSchema)[0].Data[1].value = vm.EditData[0].AMOUNT || '';
                }
            } else {
                (vm.FormSchema)[0].Data[1].value = '';
            }
        },

        RadionEnddate(data) {
            const vm = this;
            (vm.FormSchema)[0].Data[3].disabled = !data;
            if (data) {
                if (vm.EditData.length > 0) {
                    (vm.FormSchema)[0].Data[3].value =
                        vm.$options.filters.formatDate(vm.EditData[0].END_DATE);
                }
            } else {
                (vm.FormSchema)[0].Data[3].value = '';
            }
        },

        
        onSubmit(formfields, val) {
            var vm = this;
            var vm = this;
            const result = vm.$refs.ResourceRate.validateForm.validate();
            if (result) {
                debugger;
                var objData = {
                    RateScheduleId: vm.EditData.map(x => x.RATE_SCHEDULE_ID).join(","),
                    ClientId: vm.EditData.length > 0 && vm.EditData[0].CLIENT_ID > 0
                        ? vm.EditData[0].CLIENT_ID
                        : 0,

                    Rate: formfields.Rate,
                    EndDate: formfields.EndDate
                };

                debugger;
                var obj = JSON.stringify(objData);
                var params = `objData=${encodeURIComponent(obj)}`;
                DataService.BulkUpdateRate(obj).then(response => {
                    if (response.data != null) {
                        const Result = JSON.parse(response.data)[0];
                        debugger
                        if (parseInt(Result.RESULT) === 1) {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('RateSchedule')]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        } else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        }
                    }
                });

            }
        },
        Close: function () {
            this.$parent.FetchData();
            this.$parent.isBulkUpdateRate = false;
        }
    }
}
</script>
